
@font-face {
  font-family: mingTi;
  src: url('/static/fonts/WenYue-GuDianMingChaoTi-NC-W5.otf');
}
#app {
  font-family: mingTi;
}
#app h1 {
  font-family: mingTi;
}
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.navbar .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 10px;
}
.navbar a {
  display: block;
  color: #fff;
}
.nav-left a {
  padding: 10px;
}
.nav-right {
  position: relative;
}
.nav-right .nav-btn {
  display: none;
  outline: none;
}
.nav-right .menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.nav-right .menu a {
  padding: 15px;
  width: 14px;
  line-height: 22px;
  white-space: normal;
  transition: all .2s;
}
.nav-right .menu a:hover {
  color: #000;
  background-color: gainsboro;
}
.header {
  margin-bottom: 10px;
  background: no-repeat center;
  background-color: gray;
  background-size: cover;
}
.header .content {
  padding: 100px 15px 80px;
}
.header .content h1 {
  margin: 0 auto;
  width: 50px;
  line-height: 70px;
  font-size: 50px;
  color: #fff;
}
.audio-container {
  padding-right: 10px;
  margin-bottom: 10px;
}
@media only screen and (max-width: 767px) {
.navbar .content {
    padding: 0;
}
.nav-right .nav-btn {
    display: block;
    padding: 10px;
    color: #fff;
    border: none;
    background: none;
    font-size: 20px;
}
.nav-right .nav-btn:hover {
    color: #D2D2D2;
}
.nav-right .menu {
    position: absolute;
    top: 35px;
    right: 10px;
    background-color: #FFFFFF;
    box-shadow: rgba(0, 0, 0, 0.117647) 0 1px 6px, rgba(0, 0, 0, 0.239216) 0 1px 4px;
    border-radius: 2px;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: top right;
            transform-origin: top right;
    transition: -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 500ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
}
.nav-right .menu.active {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.nav-right .menu.active a {
    opacity: 1;
}
.nav-right .menu.out {
    opacity: 0;
}
.nav-right .menu a {
    opacity: 0;
    transition: all .2s, opacity .2s .2s;
    color: #777;
}
.header .content {
    padding: 60px 15px 40px;
}
.header .content h1 {
    width: 36px;
    font-size: 36px;
    line-height: 50px;
}
}



